import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import "react-native-get-random-values";
import { nanoid } from 'nanoid';


const display = {
  display: "flex",
  justifyContent: "center",
  flexDirection: "column",
};
const appTitle = {
  margin: 20,
  fontSize: 70,
  alignSelf: "center",
};
const input = {
  margin: 20,
  fontSize: 25,
  height: 40,
  width: 800,
  alignSelf: "center",
};
const submit = {
  margin: 20,
  fontSize: 20,
  height: 40,
  width: 130,
  alignSelf: "center",
};
const list = {
  fontSize: 30,
};

class ToDo extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      tasks: [],
      task: "",
    };
    this.updateTasks = this.updateTasks.bind(this);
    this.removeTask = this.removeTask.bind(this);
  }
  // Checks if stored information exists and loads them as our starting state.
  // Data is visible on our page.
  componentWillMount() {
    localStorage.getItem("tasks") &&
      this.setState({
        tasks: JSON.parse(localStorage.getItem("tasks")),
        isLoading: false,
      });
  }
  // Checks for data in local storage and we choose what data we want to use.
  componentWillUpdate(nextProps, nextState) {
    localStorage.setItem("tasks", JSON.stringify(nextState.tasks));
  }
  // Gets data of last entry.
  newTask(value) {
    this.setState({
      task: value,
    });
  }
  // Checks if last entered task is in our list of tasks.
  // Adds latest entered task throught input and becomes one of the objects in our task array and is stored locally.
  updateTasks(event) {
      event.preventDefault();
      const newTasks = [...this.state.tasks];
      this.setState(
        {
          tasks: [
            {
              name: this.state.task,
              id: nanoid(3),
            },
            ...newTasks,
          ],
          task: "",
        },
        () => {
          localStorage.setItem("tasks", JSON.stringify(this.state.tasks));
        }
      );
  }
  // Removes task from our tasks array and local storage.
  removeTask(id) {
    const newTasks = [...this.state.tasks].filter((task) => task.id !== id);
    this.setState(
      {
        tasks: [...newTasks],
      },
      () => {
        localStorage.setItem("tasks", JSON.stringify(newTasks));
      }
    );
  }

  render() {
    return (
      <form style={display}>
        <div style={appTitle}>{this.props.name}</div>
        <input
          style={input}
          type="text"
          value={this.state.task}
          onChange={(event) => this.newTask(event.target.value)}
          placeholder="  Add Task..."
        />
        <button
          style={submit}
          onClick={this.updateTasks}
          disabled={this.state.task === "" ? true : false}
        >
          Add Task
        </button>
        <ul style={list}>
          {this.state.tasks.map((item) => {
            return (
              <li key={item.id}>
                {item.name}
                <button
                  onClick={() => {
                    this.removeTask(item.id);
                  }}
                  style={submit}
                >
                  Remove
                </button>
              </li>
            );
          })}
        </ul>
      </form>
    );
  }
}
ReactDOM.render(
  <ToDo name={"ToDo Tracker"} />,
  document.getElementById("root")
);
